body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.calculator-body{
  max-width: 400px;
  height: 100vh;
  text-align: center;
  margin: auto;
}

.component{
  margin-top: 30%;
}

button {
  font-size: 2.2em;
  width: 25%;
  height: 70px;

}

.result {
  max-width: 400px;
  text-align: left;
  font-size: 2.2em;
}

.result p {
  margin: 5px;
}

